
export default function Home() {
  return (
    <div className="container mx-auto">
      <div className="flex items-center justify-center text-black">
        <Carousel
      className="w-full py-20"
      plugins={[
        Autoplay({
          delay: 2000,
        }),
      ]}
    >
      <CarouselContent>
        {Array.from({ length: 5 }).map((_, index) => (
          <CarouselItem key={index}>
            <div className="relative p-1 flex gap-6 items-stretch justify-between h-[409px]">
              <div className="p-10 flex flex-col gap-6 bg-[#F2F7FC] rounded-3xl flex-1 max-w-[425px]">
                <p className="text-[45px] font-medium">AI Agent</p>
                <p>
                  AI Agent là tập hợp các AI chuyên biệt trong hệ sinh thái
                  POSX, hoạt động như những trợ lý quản lý chuyên nghiệp. Từ
                  quản lý kho, bán hàng, marketing, đến lập kế hoạch AIGent làm
                  việc 24/7.
                </p>

                <div className="flex items-center gap-4 mt-auto">
                  <button className="border border-[#1D1B20] text-[#1D1B20] rounded-full py-2.5 px-4 ">
                    <span className="line-clamp-1">Tìm hiểu thêm</span>
                  </button>

                  <button className="bg-[#006FFF] text-white rounded-full py-2.5 px-4 flex items-center gap-2">
                    <span className="line-clamp-1">Dùng thử</span>
                    <ArrowOutward />
                  </button>
                </div>
              </div>

              <div className="flex-1 h-full">
                <Image
                  src="/images/agent.png"
                  alt="Ảnh minh họa"
                  className="rounded-3xl object-cover w-full h-full"
                  width={626}
                  height={409}
                />
              </div>
            </div>
          </CarouselItem>
        ))}
      </CarouselContent>
      <CarouselPrevious />
      <CarouselNext />
    </Carousel>
      </div>
    </div>
  );
}
